import React from 'react'
import Helmet from 'react-helmet'
import get from 'lodash/get'

const Meta = ({ site, title, desc, ogimg }) => {
  let siteTitle = get(site, 'title')
  title = title ? `${title} | ${siteTitle}` : `Yentsen Liu`
  desc = desc ? `${desc}` : `Interface/Empowerment/Democracy Design Portfolio by Yentsen Liu`
  ogimg = ogimg ? `${ogimg}` : `/img/og-image.jpg`
  return (
    <Helmet
      title={title}
      meta={[
        { name: 'twitter:card', content: 'Interface/Empowerment/Democracy Design Portfolio by Yentsen Liu' },
        {
          name: 'twitter:site',
          content: `@${get(site, 'twitter')}`,
        },
        { property: 'og:title', content: title },
        { property: 'og:type', content: 'website' },
        { property: 'og:description', content: desc },
        {
          property: 'og:url',
          content: `${get(site, 'siteUrl')}`,
        },
        {
          property: 'og:image',
          content: ogimg ,
        },
      ]}
    />
  )
}
export default Meta
